import Vue from 'vue'
import VueRouter from 'vue-router'

import Authentication from '../components/Authentication.vue'
import HelloWorld from '../components/HelloWorld.vue'
import Form from '../components/Form.vue'
import Dashboard from '../components/Dashboard.vue'
import Learning from '../components/Learning.vue'
import Admin from '../components/Admin.vue'

/* eslint-disable no-console */
Vue.use(VueRouter)

function loggedinRedirectHome(to, from, next) {
    if (localStorage.token) {
        next('/home');
    } else {
        next();
    }
}

function ifNotLoggedInRedirectHome(to, from, next ) {
    if (localStorage.token) {
        next();
    } else {
        next('/');
    }
}

function ifNotAdminRedirectHome(to, from, next) {
    if (localStorage.role === 'admin') {
        next();
    } else {
        next("/home");
    }
}

export default new VueRouter({
    mode: "history",
    routes: [
        { 
            path: '/', 
            name: 'Authentication',
            component: Authentication,
            beforeEnter: loggedinRedirectHome,
        },
        { 
            path: '/home', 
            name: 'Home',
            component: HelloWorld,
            beforeEnter: ifNotLoggedInRedirectHome,
            
        },
        { 
            path: '/form', 
            name: 'Form',
            component: Form,
            beforeEnter: ifNotLoggedInRedirectHome,

        },
        { 
            path: '/dashboard', 
            name: 'Dashboard',
            component: Dashboard,
            beforeEnter: ifNotLoggedInRedirectHome,

        },
        { 
            path: '/learning', 
            name: 'Learning',
            component: Learning,
            beforeEnter: ifNotLoggedInRedirectHome,

        },
        { 
            path: '/admin', 
            name: 'Admin',
            component: Admin,
            beforeEnter: ifNotAdminRedirectHome, ifNotLoggedInRedirectHome

        },
        { 
            path: "*", 
            redirect: "/"
        }
        
    ]
})